import request from '../util/request'


export default {

    // 
  getAvgData() {
    return request({
        url: '/historyData/avgData',
        method: 'get',
    })
  },

  // 
  getTimeRangeData(startTime,endTime,turbineType) {
    return request({
        url: '/historyData/timeRange',
        method: 'get',
        params: {
          startDate:startTime,
          endDate:endTime,
          turbineType:turbineType
        },
    })
  },

  // 
  getTimeRangeMaxData(startTime,endTime,turbineType) {
    return request({
        url: '/historyData/timeRangeMaxData',
        method: 'get',
        params: {
            startDate:startTime,
            endDate:endTime,
            turbineType:turbineType
        },
    })
  },

  // 
  getPowerRankData(month) {
    return request({
        url: '/historyData/powerRank',
        params:{
            month:month
        },
        method: 'get',
    })
  },

  // 
  getRecentData() {
    return request({
        url: '/historyData/recentData',
        method: 'get',
    })
  },

}
