import request from '../util/request'


export default {
    // 
    getWeatherData(lat, lon) {
        return request({
            url: '/locationPre/weather',
            method: 'get',
            params: {
                lat: lat,
                lon: lon
            }
        })
    },

    // 
    getFuturePreData(lat, lon) {
    return request({
        url: '/locationPre/futurePre',
        method: 'get',
        params: {
            lat: lat,
            lon: lon
        }
    })
  }}